	module.exports = {"data":{"post":{"html":"<p>Last weekend I was finally able to attend the annual Whiny Expat Blogger Meetup, organized the past 4 years by <a href=\"http://jbittner.com/germany/\">J</a>. Good thing, since when we were voting on the location it was my idea to stuff the ballot box for Bremen so we'd finally have a northern location. So while most folks had to travel for hours to arrive, I could just hop on the <a href=\"http://www.der-metronom.de/\">Metronom</a> for a 45 minute train trip.</p>\n<p><a href=\"http://claireseuroamerica.blogspot.com/\">Claire</a> organized the details in Bremen and did a wonderful job, even preparing little welcome kits for us all. She herded our group of 25 (including strollers) through the city, parked us on the Weser riverbank to enjoy the sun for lunch, and reserved excellent places for dinner and for Sunday brunch. She didn't plan the excursion to the back room of a gay bar Saturday night, that just happened.</p>\n<p>Our foreign experience ranged from brand-new to old-hat, so we had people all along the <a href=\"http://en.wikipedia.org/wiki/Five_stages_of_grief\">5 stages of being an expat</a>. We old-timers couldn't promise the newcomers that things would get better with time, but we could reassure them that they would eventually get used to it (final stage: acceptance).</p>\n<p>I'm horrible at names, so I won't try to mention everyone I met, and I know I didn't get the chance to talk to everyone. I will mention <a href=\"http://www.elmada.com/\">Adam</a> and <a href=\"http://justcallmemausi.blogspot.com\">Mausi</a>, both of whom I've read for ages and I was finally able to meet. Most people seem to be shy about their names, so I'll just say it was nice meeting you all, and I've added your blogs to my feed reader. And I hope to see you all next time, be it in Berlin or Regensburg, or wherever.</p>","fields":{"title":"Vintage Expats","date":"02 Oct 2008","url":"https://papascott.de/2008-10-02-vintage-expats/"}}},"pathContext":{"slug":"/2008-10-02-vintage-expats/","prev":{"fields":{"title":"Oslo Cruise","date":"08 Sep 2008","slug":"/2008-09-08-oslo-cruise/"}},"next":{"fields":{"title":"Easy Morning for Free","date":"11 Oct 2008","slug":"/2008-10-11-easy-morning-for-free/"}}}}

